interface OctoBrowserConfig {
  apiKey: string;
  baseUrl: string;
}

interface ProfileConfig {
  name: string;
  browser: string;
  os: string;
  userAgent?: string;
  proxy?: {
    type: string;
    host: string;
    port: number;
    username?: string;
    password?: string;
  };
  fingerprint?: {
    screen?: {
      width: number;
      height: number;
    };
    timezone?: string;
    locale?: string;
    webrtc?: string;
    canvas?: string;
    webgl?: string;
  };
}

interface OctoBrowserResponse {
  success: boolean;
  data?: any;
  error?: string;
}

interface StartProfileResponse {
  success: boolean;
  data?: {
    uuid: string;
    ws_endpoint: string;
    selenium_port: number;
    vnc_port: number;
    status: string;
  };
  error?: string;
}

class OctoBrowserAPI {
  private config: OctoBrowserConfig;

  constructor() {
    this.config = {
      apiKey: 'abc2426ceacd42c6b77670800b456c75',
      baseUrl: 'http://localhost:58888'
    };
  }

  // 获取可用的屏幕分辨率
  async getAvailableScreens(): Promise<OctoBrowserResponse> {
    try {
      const url = `${this.config.baseUrl}/api/v2/automation/fingerprint/screens`;
      
      const response = await fetch(url, {
        method: 'GET',
        headers: {
          'X-Octo-Api-Token': this.config.apiKey
        }
      });

      const data = await response.json();
      
      if (response.ok) {
        return {
          success: true,
          data: data
        };
      } else {
        return {
          success: false,
          error: data.error || 'Failed to get available screens'
        };
      }
    } catch (error) {
      return {
        success: false,
        error: `Network error: ${error instanceof Error ? error.message : 'Unknown error'}`
      };
    }
  }

  // 创建一次性配置文件并启动
  async createOneTimeProfile(phoneNumber?: string): Promise<StartProfileResponse> {
    try {
      // 首先获取可用的屏幕分辨率
      const screensResult = await this.getAvailableScreens();
      let screenConfig = { width: 1920, height: 1080 }; // 默认分辨率
      
      if (screensResult.success && screensResult.data?.length > 0) {
        // 选择一个随机的屏幕分辨率
        const randomScreen = screensResult.data[Math.floor(Math.random() * screensResult.data.length)];
        screenConfig = {
          width: randomScreen.width,
          height: randomScreen.height
        };
      }

      // 创建一次性配置文件
      const profileConfig: ProfileConfig = {
        name: `WhatsApp-OneTime-${Date.now()}`,
        browser: 'chrome',
        os: 'windows',
        fingerprint: {
          screen: screenConfig,
          timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
          locale: navigator.language,
          webrtc: 'real',
          canvas: 'real',
          webgl: 'real'
        }
      };

      // 如果提供了电话号码，可以在配置中添加相关信息
      if (phoneNumber) {
        profileConfig.name = `WhatsApp-${phoneNumber.replace(/\D/g, '')}-${Date.now()}`;
      }

      const createUrl = `${this.config.baseUrl}/api/v2/automation/browser/create`;
      
      const createResponse = await fetch(createUrl, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'X-Octo-Api-Token': this.config.apiKey
        },
        body: JSON.stringify(profileConfig)
      });

      const createData = await createResponse.json();
      
      if (!createResponse.ok) {
        return {
          success: false,
          error: createData.error || 'Failed to create profile'
        };
      }

      const profileUuid = createData.uuid;

      // 启动创建的配置文件
      const startResult = await this.startProfile(profileUuid);
      
      if (startResult.success) {
        // 启动成功后，导航到 WhatsApp Web
        setTimeout(async () => {
          await this.navigateToWhatsApp(profileUuid);
        }, 3000); // 等待3秒让浏览器完全启动
      }

      return startResult;
    } catch (error) {
      return {
        success: false,
        error: `Failed to create one-time profile: ${error instanceof Error ? error.message : 'Unknown error'}`
      };
    }
  }

  // 启动配置文件
  async startProfile(profileId?: string): Promise<StartProfileResponse> {
    try {
      const url = `${this.config.baseUrl}/api/v2/automation/browser/start`;
      
      const payload = profileId ? { uuid: profileId } : {};
      
      const response = await fetch(url, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'X-Octo-Api-Token': this.config.apiKey
        },
        body: JSON.stringify(payload)
      });

      const data = await response.json();
      
      if (response.ok) {
        return {
          success: true,
          data: {
            uuid: data.uuid,
            ws_endpoint: data.ws_endpoint,
            selenium_port: data.selenium_port,
            vnc_port: data.vnc_port,
            status: data.status
          }
        };
      } else {
        return {
          success: false,
          error: data.error || 'Failed to start browser profile'
        };
      }
    } catch (error) {
      return {
        success: false,
        error: `Network error: ${error instanceof Error ? error.message : 'Unknown error'}`
      };
    }
  }

  // 导航到 WhatsApp Web
  async navigateToWhatsApp(profileUuid: string): Promise<OctoBrowserResponse> {
    try {
      const url = `${this.config.baseUrl}/api/v2/automation/browser/navigate`;
      
      const response = await fetch(url, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'X-Octo-Api-Token': this.config.apiKey
        },
        body: JSON.stringify({
          uuid: profileUuid,
          url: 'https://web.whatsapp.com'
        })
      });

      const data = await response.json();
      
      if (response.ok) {
        return {
          success: true,
          data: data
        };
      } else {
        return {
          success: false,
          error: data.error || 'Failed to navigate to WhatsApp'
        };
      }
    } catch (error) {
      return {
        success: false,
        error: `Navigation error: ${error instanceof Error ? error.message : 'Unknown error'}`
      };
    }
  }

  // 在 WhatsApp Web 中输入电话号码
  async inputPhoneNumber(profileUuid: string, phoneNumber: string): Promise<OctoBrowserResponse> {
    try {
      const url = `${this.config.baseUrl}/api/v2/automation/browser/execute`;
      
      // 等待页面加载并输入电话号码的脚本
      const script = `
        // 等待页面加载
        await new Promise(resolve => setTimeout(resolve, 5000));
        
        // 查找电话号码输入框
        const phoneInput = document.querySelector('input[type="tel"]') || 
                          document.querySelector('input[data-testid="phone-number-input"]') ||
                          document.querySelector('input[placeholder*="phone"]') ||
                          document.querySelector('input[placeholder*="number"]');
        
        if (phoneInput) {
          // 清空输入框
          phoneInput.value = '';
          phoneInput.focus();
          
          // 输入电话号码
          phoneInput.value = '${phoneNumber}';
          
          // 触发输入事件
          phoneInput.dispatchEvent(new Event('input', { bubbles: true }));
          phoneInput.dispatchEvent(new Event('change', { bubbles: true }));
          
          // 查找并点击下一步按钮
          await new Promise(resolve => setTimeout(resolve, 1000));
          
          const nextButton = document.querySelector('button[data-testid="phone-number-confirm"]') ||
                           document.querySelector('button:contains("Next")') ||
                           document.querySelector('button:contains("下一步")') ||
                           document.querySelector('button[type="submit"]');
          
          if (nextButton && !nextButton.disabled) {
            nextButton.click();
            return { success: true, message: 'Phone number entered and submitted' };
          } else {
            return { success: false, message: 'Next button not found or disabled' };
          }
        } else {
          return { success: false, message: 'Phone input field not found' };
        }
      `;

      const response = await fetch(url, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'X-Octo-Api-Token': this.config.apiKey
        },
        body: JSON.stringify({
          uuid: profileUuid,
          script: script
        })
      });

      const data = await response.json();
      
      if (response.ok) {
        return {
          success: true,
          data: data
        };
      } else {
        return {
          success: false,
          error: data.error || 'Failed to input phone number'
        };
      }
    } catch (error) {
      return {
        success: false,
        error: `Script execution error: ${error instanceof Error ? error.message : 'Unknown error'}`
      };
    }
  }

  // 在 WhatsApp Web 中输入验证码
  async inputVerificationCode(profileUuid: string, code: string): Promise<OctoBrowserResponse> {
    try {
      const url = `${this.config.baseUrl}/api/v2/automation/browser/execute`;
      
      const script = `
        // 等待验证码输入框出现
        await new Promise(resolve => setTimeout(resolve, 2000));
        
        // 查找验证码输入框
        const codeInputs = document.querySelectorAll('input[type="text"]') ||
                          document.querySelectorAll('input[data-testid*="code"]') ||
                          document.querySelectorAll('input[maxlength="1"]');
        
        if (codeInputs.length >= 6) {
          // 如果是分别的输入框（每位一个）
          const codeDigits = '${code}'.split('');
          for (let i = 0; i < Math.min(6, codeDigits.length); i++) {
            if (codeInputs[i]) {
              codeInputs[i].value = codeDigits[i];
              codeInputs[i].dispatchEvent(new Event('input', { bubbles: true }));
              codeInputs[i].dispatchEvent(new Event('change', { bubbles: true }));
            }
          }
          return { success: true, message: 'Verification code entered' };
        } else {
          // 查找单个验证码输入框
          const singleCodeInput = document.querySelector('input[placeholder*="code"]') ||
                                 document.querySelector('input[placeholder*="验证"]') ||
                                 document.querySelector('input[type="tel"]');
          
          if (singleCodeInput) {
            singleCodeInput.value = '${code}';
            singleCodeInput.dispatchEvent(new Event('input', { bubbles: true }));
            singleCodeInput.dispatchEvent(new Event('change', { bubbles: true }));
            return { success: true, message: 'Verification code entered in single input' };
          } else {
            return { success: false, message: 'Verification code input not found' };
          }
        }
      `;

      const response = await fetch(url, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'X-Octo-Api-Token': this.config.apiKey
        },
        body: JSON.stringify({
          uuid: profileUuid,
          script: script
        })
      });

      const data = await response.json();
      
      if (response.ok) {
        return {
          success: true,
          data: data
        };
      } else {
        return {
          success: false,
          error: data.error || 'Failed to input verification code'
        };
      }
    } catch (error) {
      return {
        success: false,
        error: `Script execution error: ${error instanceof Error ? error.message : 'Unknown error'}`
      };
    }
  }

  // 停止配置文件
  async stopProfile(profileId: string): Promise<OctoBrowserResponse> {
    try {
      const url = `${this.config.baseUrl}/api/v2/automation/browser/stop`;
      
      const response = await fetch(url, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'X-Octo-Api-Token': this.config.apiKey
        },
        body: JSON.stringify({ uuid: profileId })
      });

      const data = await response.json();
      
      if (response.ok) {
        return {
          success: true,
          data: data
        };
      } else {
        return {
          success: false,
          error: data.error || 'Failed to stop browser profile'
        };
      }
    } catch (error) {
      return {
        success: false,
        error: `Network error: ${error instanceof Error ? error.message : 'Unknown error'}`
      };
    }
  }

  // 删除配置文件
  async deleteProfile(profileId: string): Promise<OctoBrowserResponse> {
    try {
      const url = `${this.config.baseUrl}/api/v2/automation/browser/delete`;
      
      const response = await fetch(url, {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
          'X-Octo-Api-Token': this.config.apiKey
        },
        body: JSON.stringify({ uuid: profileId })
      });

      const data = await response.json();
      
      if (response.ok) {
        return {
          success: true,
          data: data
        };
      } else {
        return {
          success: false,
          error: data.error || 'Failed to delete browser profile'
        };
      }
    } catch (error) {
      return {
        success: false,
        error: `Network error: ${error instanceof Error ? error.message : 'Unknown error'}`
      };
    }
  }

  // 获取配置文件列表
  async getProfiles(): Promise<OctoBrowserResponse> {
    try {
      const url = `${this.config.baseUrl}/api/v2/automation/browser/profiles`;
      
      const response = await fetch(url, {
        method: 'GET',
        headers: {
          'X-Octo-Api-Token': this.config.apiKey
        }
      });

      const data = await response.json();
      
      if (response.ok) {
        return {
          success: true,
          data: data
        };
      } else {
        return {
          success: false,
          error: data.error || 'Failed to get profiles'
        };
      }
    } catch (error) {
      return {
        success: false,
        error: `Network error: ${error instanceof Error ? error.message : 'Unknown error'}`
      };
    }
  }

  // 获取配置文件状态
  async getProfileStatus(profileId: string): Promise<OctoBrowserResponse> {
    try {
      const url = `${this.config.baseUrl}/api/v2/automation/browser/status/${profileId}`;
      
      const response = await fetch(url, {
        method: 'GET',
        headers: {
          'X-Octo-Api-Token': this.config.apiKey
        }
      });

      const data = await response.json();
      
      if (response.ok) {
        return {
          success: true,
          data: data
        };
      } else {
        return {
          success: false,
          error: data.error || 'Failed to get profile status'
        };
      }
    } catch (error) {
      return {
        success: false,
        error: `Network error: ${error instanceof Error ? error.message : 'Unknown error'}`
      };
    }
  }
}

export const octoBrowserAPI = new OctoBrowserAPI();
export type { ProfileConfig, OctoBrowserResponse, StartProfileResponse };