import { useEffect, useState } from 'react';
import { translations } from '../i18n/translations';

type SupportedLanguage = keyof typeof translations;

export function useLanguage() {
  const [currentLanguage, setCurrentLanguage] = useState<SupportedLanguage>('zh'); // 默认中文

  useEffect(() => {
    // 检测并设置语言
    const detectAndSetLanguage = () => {
      // 首先检查本地存储
      const savedLanguage = localStorage.getItem('wa_language');
      if (savedLanguage && translations[savedLanguage as SupportedLanguage]) {
        setCurrentLanguage(savedLanguage as SupportedLanguage);
        console.log('使用保存的语言:', savedLanguage);
        return;
      }

      // 获取用户的语言设置
      const userLanguages = navigator.languages || [navigator.language];
      console.log('检测到的浏览器语言:', userLanguages);
      
      // 精确语言匹配映射
      const languageMap: Record<string, SupportedLanguage> = {
        // 中文变体
        'zh': 'zh',
        'zh-cn': 'zh',
        'zh-hans': 'zh',
        'zh-sg': 'zh',
        'zh-tw': 'zh-TW',
        'zh-hk': 'zh-TW',
        'zh-mo': 'zh-TW',
        'zh-hant': 'zh-TW',
        
        // 英语变体
        'en': 'en',
        'en-us': 'en',
        'en-gb': 'en',
        'en-au': 'en',
        'en-ca': 'en',
        'en-nz': 'en',
        'en-za': 'en',
        
        // 东南亚语言
        'ms': 'ms',
        'ms-my': 'ms',
        'ms-bn': 'ms',
        'km': 'km',
        'km-kh': 'km',
        'id': 'id',
        'id-id': 'id',
        'tl': 'tl',
        'fil': 'tl',
        'tl-ph': 'tl',
        'vi': 'vi',
        'vi-vn': 'vi',
        'th': 'th',
        'th-th': 'th',
        
        // 东亚语言
        'ja': 'ja',
        'ja-jp': 'ja',
        'ko': 'ko',
        'ko-kr': 'ko',
        
        // 欧洲语言
        'es': 'es',
        'es-es': 'es',
        'es-mx': 'es',
        'es-ar': 'es',
        'es-co': 'es',
        'es-cl': 'es',
        'fr': 'fr',
        'fr-fr': 'fr',
        'fr-ca': 'fr',
        'fr-be': 'fr',
        'fr-ch': 'fr',
        'de': 'de',
        'de-de': 'de',
        'de-at': 'de',
        'de-ch': 'de',
        'it': 'it',
        'it-it': 'it',
        'it-ch': 'it',
        'pt': 'pt',
        'pt-br': 'pt',
        'pt-pt': 'pt',
        'ru': 'ru',
        'ru-ru': 'ru',
        'ru-by': 'ru',
        'ru-kz': 'ru',
        
        // 中东和南亚语言
        'ar': 'ar',
        'ar-sa': 'ar',
        'ar-ae': 'ar',
        'ar-eg': 'ar',
        'ar-ma': 'ar',
        'hi': 'hi',
        'hi-in': 'hi',
        'tr': 'tr',
        'tr-tr': 'tr',
        'bn': 'bn',
        'bn-bd': 'bn',
        'bn-in': 'bn',
        'ur': 'ur',
        'ur-pk': 'ur',
        'ur-in': 'ur',
        'fa': 'fa',
        'fa-ir': 'fa',
        'fa-af': 'fa',
        'he': 'he',
        'he-il': 'he'
      };
      
      // 遍历用户的语言偏好列表
      for (const userLang of userLanguages) {
        const normalizedLang = userLang.toLowerCase();
        console.log('检查语言:', normalizedLang);
        
        // 检查完整语言代码匹配
        if (languageMap[normalizedLang]) {
          const detectedLang = languageMap[normalizedLang];
          console.log('匹配到语言:', detectedLang);
          setCurrentLanguage(detectedLang);
          localStorage.setItem('wa_language', detectedLang);
          return;
        }
        
        // 检查语言前缀匹配
        const baseLanguage = normalizedLang.split('-')[0];
        if (languageMap[baseLanguage]) {
          const detectedLang = languageMap[baseLanguage];
          console.log('通过前缀匹配到语言:', detectedLang);
          setCurrentLanguage(detectedLang);
          localStorage.setItem('wa_language', detectedLang);
          return;
        }
      }
      
      // 如果都没有匹配，根据地理位置推测
      const fallbackByRegion = () => {
        try {
          const timezone = Intl.DateTimeFormat().resolvedOptions().timeZone;
          console.log('检测到时区:', timezone);
          
          let detectedLang: SupportedLanguage = 'zh'; // 默认中文
          
          // 根据时区推测地区和语言
          if (timezone.includes('Asia/Shanghai') || timezone.includes('Asia/Beijing')) {
            detectedLang = 'zh';
          } else if (timezone.includes('Asia/Taipei') || timezone.includes('Asia/Hong_Kong')) {
            detectedLang = 'zh-TW';
          } else if (timezone.includes('Asia/Tokyo')) {
            detectedLang = 'ja';
          } else if (timezone.includes('Asia/Seoul')) {
            detectedLang = 'ko';
          } else if (timezone.includes('Asia/Bangkok')) {
            detectedLang = 'th';
          } else if (timezone.includes('Asia/Ho_Chi_Minh')) {
            detectedLang = 'vi';
          } else if (timezone.includes('Asia/Jakarta')) {
            detectedLang = 'id';
          } else if (timezone.includes('Asia/Kuala_Lumpur')) {
            detectedLang = 'ms';
          } else if (timezone.includes('Asia/Manila')) {
            detectedLang = 'tl';
          } else if (timezone.includes('Asia/Phnom_Penh')) {
            detectedLang = 'km';
          } else if (timezone.includes('Asia/Kolkata') || timezone.includes('Asia/Delhi')) {
            detectedLang = 'hi';
          } else if (timezone.includes('Asia/Dhaka')) {
            detectedLang = 'bn';
          } else if (timezone.includes('Asia/Karachi')) {
            detectedLang = 'ur';
          } else if (timezone.includes('Asia/Tehran')) {
            detectedLang = 'fa';
          } else if (timezone.includes('Asia/Istanbul')) {
            detectedLang = 'tr';
          } else if (timezone.includes('Asia/Riyadh') || timezone.includes('Asia/Dubai')) {
            detectedLang = 'ar';
          } else if (timezone.includes('Asia/Jerusalem')) {
            detectedLang = 'he';
          } else if (timezone.includes('Europe/Moscow')) {
            detectedLang = 'ru';
          } else if (timezone.includes('Europe/Berlin') || timezone.includes('Europe/Vienna')) {
            detectedLang = 'de';
          } else if (timezone.includes('Europe/Paris') || timezone.includes('Europe/Brussels')) {
            detectedLang = 'fr';
          } else if (timezone.includes('Europe/Madrid') || timezone.includes('Europe/Barcelona')) {
            detectedLang = 'es';
          } else if (timezone.includes('Europe/Rome')) {
            detectedLang = 'it';
          } else if (timezone.includes('Europe/Lisbon')) {
            detectedLang = 'pt';
          } else if (timezone.includes('America/Sao_Paulo') || timezone.includes('America/Rio')) {
            detectedLang = 'pt';
          } else if (timezone.includes('America/Mexico_City') || timezone.includes('America/Buenos_Aires')) {
            detectedLang = 'es';
          } else if (timezone.includes('America/') || timezone.includes('Europe/London')) {
            detectedLang = 'en';
          }
          
          console.log('根据时区设置语言:', detectedLang);
          setCurrentLanguage(detectedLang);
          localStorage.setItem('wa_language', detectedLang);
        } catch (error) {
          console.warn('时区检测失败，使用默认中文');
          setCurrentLanguage('zh');
          localStorage.setItem('wa_language', 'zh');
        }
      };
      
      fallbackByRegion();
    };

    detectAndSetLanguage();
  }, []);

  const setLanguage = (lang: SupportedLanguage) => {
    console.log('手动设置语言:', lang);
    setCurrentLanguage(lang);
    localStorage.setItem('wa_language', lang);
  };

  // 确保返回有效的翻译对象
  const currentTranslations = translations[currentLanguage] || translations['zh'];

  return {
    t: currentTranslations,
    language: currentLanguage,
    setLanguage
  };
}